import React, {forwardRef, useImperativeHandle, useState} from 'react';
import {
    DropEvent,
    ExpandableSection,
    FileUpload,
    FormGroup,
    FormHelperText,
    HelperText,
    HelperTextItem
} from '@patternfly/react-core';

import _ from 'lodash';
import './saml-metadata-file.scss'
import PropTypes from "prop-types";
import { XMLParser } from 'fast-xml-parser';

import {formatMessage} from "../../../Utils/message-formatter-utils";
import {FlowStep} from "../../../Apps/IdpSelfService/Constants/flow-step";
import {SamlMetadataConstants} from "../../../Apps/IdpSelfService/Constants/saml-metadata-constants";
import {SamlMetadataSchemaAttributes} from "../../../Apps/IdpSelfService/CommonInterfaces/saml-metadata-file";
import {InputValidation} from "../../../Apps/IdpSelfService/CommonInterfaces/input-validation";
import {UserChoices} from "../../../Apps/IdpSelfService/CommonInterfaces/user-choices";
import {
    AUTH_REQUEST_URL,
    ENTITY_ID, X509_CERTIFICATE
} from "../../../Apps/IdpSelfService/CommonInterfaces/idp-request-types";
import {containsPotentialThreats} from "../../../Utils/xml-utils";
import {SamlMetadataMessages} from "../../../Components/FileUpload/SamlMetadataFile/saml-metadata-messages";

interface Props {
    updateStatus: Function;
    resetNextClicked: Function;
    rootValidation: React.MutableRefObject<InputValidation>;
    rootUserChoices: React.MutableRefObject<UserChoices>;
    maxKBSize: number;
}

export const SamlMetadataFile: React.FunctionComponent<Props> = forwardRef(({updateStatus, resetNextClicked, rootValidation, rootUserChoices, maxKBSize}: Props, ref) => {
    const allowedFileTypes = ['text/xml'];

    const [file, setFile] = useState<File | null>(null);
    const [value, setValue] = useState('');
    const [filename, setFilename] = useState('');

    const [errorMessage, setErrorMessage] = useState<string | null>(null);
    const [successMessage, setSuccessMessage] = useState<string | null>(null);

    const [isFileSizeRejected, setIsFileSizeRejected] = useState(false);
    const [isExpanded, setIsExpanded] = useState(false);
    const [isSafe, setIsSafe] = useState(false);

    /*
     * Add the following code to the import-saml-from-file.tsx (or the parent component)
     * if you need to do extra handling of the file object or any attribute used in this component:
     *const samlMetadataFileRef = useRef<any>(null);
     *if (samlMetadataFileRef.current) {
     *  const file = samlMetadataFileRef.current.getFile();
     *}
     */
    useImperativeHandle(ref, () => ({
        getFile: () => file
    }));

    function clear() {
        setFile(null);
        setValue('');
        setFilename('');
        setSuccessMessage('');
        setErrorMessage('');
        setIsFileSizeRejected(false);
    }

    // eslint-disable-next-line no-shadow
    const onToggle = (_event: React.MouseEvent, isExpanded: boolean) => {
        setIsExpanded(isExpanded);
    };

    function isValid(entityID: string | null, location: string | null, certificates: Set<string>) {
        if ((!_.isString(entityID) || !_.isString(location) || certificates.size === 0) || (entityID.length === 0 || location.length === 0)) {
            return false;
        }
        return true;
    }

    /**
     * XMLParser docs:
     * https://github.com/NaturalIntelligence/fast-xml-parser/tree/master/docs/v5
     * Config options:
     * https://github.com/NaturalIntelligence/fast-xml-parser/blob/master/docs/v4/2.XMLparseOptions.md
     * https://github.com/NaturalIntelligence/fast-xml-parser/blob/master/docs/v5/3.Options.md
     */
    // eslint-disable-next-line no-shadow
    function parse(xml: string, file: File) {
        // regardless of current state. if the user alters the input clear existing flags.
        resetNextClicked(false);

        function extractEntityID(parsedXml: SamlMetadataSchemaAttributes) {
            if (parsedXml?.EntityDescriptor?.[SamlMetadataConstants.ENTITY_ID]) {
                return parsedXml.EntityDescriptor[SamlMetadataConstants.ENTITY_ID];
            }
            return null;
        }

        function extractLocation(services: any) {
            if (services || Array.isArray(services)) {
                const httpPostService = services.find((service: any) => service[SamlMetadataConstants.BINDING] === SamlMetadataConstants.HTTP_POST_BINDING);
                const httpRedirectService = services.find((service: any) => service[SamlMetadataConstants.BINDING] === SamlMetadataConstants.HTTP_REDIRECT_BINDING);

                // see: org.keycloak.broker.saml.SAMLIdentityProviderFactory#parseConfig
                if (httpPostService?.[SamlMetadataConstants.LOCATION]) {
                    return httpPostService[SamlMetadataConstants.LOCATION];
                } else if (httpRedirectService?.[SamlMetadataConstants.LOCATION]) {
                    return httpRedirectService[SamlMetadataConstants.LOCATION];
                }
            }
            return null;
        }

        function extractX509Certificate(keyDescriptors: any) {
            const certificates: Set<string> = new Set<string>();
            if (keyDescriptors) {
                // ensure we handle both single and multiple KeyDescriptor entries
                for (const keyDescriptor of Array.isArray(keyDescriptors) ? keyDescriptors : [keyDescriptors]) {
                    const x509Data = keyDescriptor?.[SamlMetadataConstants.KEY_INFO]?.[SamlMetadataConstants.X509_DATA];
                    const dsX509Data = keyDescriptor?.[SamlMetadataConstants.DS_KEY_INFO]?.[SamlMetadataConstants.DS_X509_DATA];

                    let certificatesArray = []

                    // we need to handle both single and multiple <X509Certificate> entries
                    if (x509Data) {
                        certificatesArray = Array.isArray(x509Data?.[SamlMetadataConstants.X509_CERTIFICATE])
                            ? x509Data[SamlMetadataConstants.X509_CERTIFICATE]
                            : [x509Data?.[SamlMetadataConstants.X509_CERTIFICATE]];
                    } else if (dsX509Data) {
                        // maybe the metadata file uses <ds:XXX> as a prefix, so let's try handling w/ DS entries instead
                        certificatesArray = Array.isArray(dsX509Data?.[SamlMetadataConstants.DS_X509_CERTIFICATE])
                            ? dsX509Data[SamlMetadataConstants.DS_X509_CERTIFICATE]
                            : [dsX509Data?.[SamlMetadataConstants.DS_X509_CERTIFICATE]];
                    }

                    // add the certificates found (ignores null/undefined entries)
                    for (const certificate of certificatesArray) {
                        if (certificate) {
                            certificates.add(certificate);
                        }
                    }
                }
            }
            return certificates;
        }

        try {
            const parser = new XMLParser({
                // ensure that attributes like SamlMetadataConstants.ENTITY_ID are captured and not ignored
                ignoreAttributes: false,
                // allows the parser to handle boolean attributes in the XML
                allowBooleanAttributes: true,
                attributeNamePrefix: '@_',
                parseAttributeValue: true,
                // ignores processing instructions <? .. ?> in the XML file as they are irrelevant.
                ignorePiTags: true
            });

            const parsedXml: SamlMetadataSchemaAttributes = parser.parse(xml);
            const keyDescriptors = parsedXml?.EntityDescriptor?.IDPSSODescriptor?.KeyDescriptor || [];
            const services = parsedXml?.EntityDescriptor?.IDPSSODescriptor?.SingleSignOnService;

            const entityID = extractEntityID(parsedXml);
            const location = extractLocation(services);
            const certificates: Set<string> = extractX509Certificate(keyDescriptors);

            if (!isValid(entityID, location, certificates)) {
                throw new Error(SamlMetadataMessages.INVALID_OR_MISSING_DATA);
            }

            //  update the main data storage object with the parsed values
            rootUserChoices.current.selections[ENTITY_ID] = entityID as string;
            rootUserChoices.current.selections[AUTH_REQUEST_URL] = location as string;
            const cert = certificates.values().next();
            rootUserChoices.current.selections[X509_CERTIFICATE] = cert.value;

            rootValidation.current.IMPORT_SAML_FROM_FILE = true;
            rootValidation.current.IMPORT_SAML_FROM_URL = false;

            updateStatus(FlowStep.FINAL_STEP);

            setIsFileSizeRejected(false);
            setFile(file);
            setErrorMessage(null);
            setSuccessMessage(formatMessage(SamlMetadataMessages.FILE_SUCCESSFULLY_LOADED, file.name, (file.size / 1024).toFixed(2)));
            setIsSafe(true);
        } catch (parseError) {
            setIsSafe(false);
            setErrorMessage(formatMessage(SamlMetadataMessages.FILE_PARSING_ERROR, (parseError as Error).message));
        }
    }

    // eslint-disable-next-line no-shadow
    function isFileSizeInvalid(file: File) {
        if (!allowedFileTypes.includes(file.type)) {
            setErrorMessage(SamlMetadataMessages.INVALID_FILE_TYPE);
            setSuccessMessage(null);
            return true;
        }
        if (file.size > (maxKBSize * 1024)) {
            setIsFileSizeRejected(true);
            return true;
        }
        return false;
    }

    // eslint-disable-next-line no-shadow
    const handleFileInputChange = (_: any, file: File) => {
        clear();
        setFilename(file.name);

        if (file) {
            const reader = new FileReader();

            reader.onerror = () => {
                setIsSafe(false);
                setErrorMessage(SamlMetadataMessages.FILE_READ_ERROR);
            }

            reader.onload = (event: ProgressEvent<FileReader>) => {
                const xmlString = event.target?.result as string;
                if (containsPotentialThreats(xmlString)) {
                    setIsSafe(false);
                    setErrorMessage(SamlMetadataMessages.MALICIOUS_FILE_DETECTED);
                } else if (!isFileSizeInvalid(file)) {
                    parse(xmlString, file);
                }
            };

            reader.readAsText(file);
        }
    };

    // eslint-disable-next-line no-shadow
    const handleDataChange = (_event: DropEvent, value: string) => {
        setValue(value);
    };

    // eslint-disable-next-line no-unused-vars
    const handleClear = (_event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        clear();
        setIsExpanded(false);
    };

    const handleFileRejected = () => {
        setIsFileSizeRejected(true);
    };

    // eslint-disable-next-line no-unused-vars
    const handleFileReadStarted = (_event: DropEvent, _fileHandle: File) => {
        setErrorMessage('');
        setSuccessMessage('');
    };

    // eslint-disable-next-line no-unused-vars
    const handleFileReadFinished = (_event: DropEvent, _fileHandle: File) => {
        setIsExpanded(true);
    };

    return (
        <FormGroup fieldId="xml-file-with-restrictions">
            <FileUpload
                id="xml-file-with-restrictions"
                type="text"
                value={value}
                filename={filename}
                filenamePlaceholder="Drag and drop the SAML metadata file or upload one"
                onFileInputChange={handleFileInputChange}
                onDataChange={handleDataChange}
                onReadStarted={handleFileReadStarted}
                onReadFinished={handleFileReadFinished}
                onClearClick={handleClear}
                dropzoneProps={{
                    accept: {'text/xml': ['.xml']},
                    maxSize: (maxKBSize * 1024),
                    onDropRejected: handleFileRejected
                }}
                validated={isFileSizeRejected ? 'error' : 'default'}
                browseButtonText="Upload"
                // this will hide the core component code preview block (which doesn't support syntax highlighting)
                hideDefaultPreview/>
            <FormHelperText>
                <HelperText>
                    <HelperTextItem variant={isFileSizeRejected ? 'error' : 'default'}>
                        {isFileSizeRejected ? formatMessage(SamlMetadataMessages.FILE_TOO_LARGE, maxKBSize) : formatMessage(SamlMetadataMessages.SAML_XML_REQUIREMENTS, maxKBSize)}
                    </HelperTextItem>
                    {(successMessage || errorMessage) && (
                        <HelperTextItem variant={errorMessage ? 'error' : 'success'}>
                            {errorMessage ?? successMessage}
                        </HelperTextItem>
                    )}
                </HelperText>
            </FormHelperText>
            <div className="xml-highlighter">
                {isSafe && value && (
                    <ExpandableSection toggleText={isExpanded ? 'Show less' : 'Show more'} onToggle={onToggle} isExpanded={isExpanded}>
                        <div className="highlighter-panel">
                            {value}
                        </div>
                    </ExpandableSection>
                )}
            </div>
        </FormGroup>
    );
});

SamlMetadataFile.displayName = "SamlMetadataFile";

SamlMetadataFile.propTypes = {
    maxKBSize: PropTypes.number.isRequired
}

export default SamlMetadataFile;
