// Final page to display the urls of the flow https://miro.com/app/board/uXjVO_x__QE=/

import {ClipboardCopy, Form, FormGroup, Text, Title} from '@patternfly/react-core';
import React, {useEffect, useState} from 'react';
import PropTypes from 'prop-types';
import {useChrome} from "@redhat-cloud-services/frontend-components/useChrome";
import {getOrgId, getSSOBaseUrl} from "../../../Utils/general-utils";
interface Props{
    accessToken: string | null;
}
const ImportSamlFromURL = ({accessToken} : Props) => {
    const [orgId, setOrgId] = useState('');
    const chrome = useChrome();

    const getConfigURL = (path: string) => {
        return getSSOBaseUrl(chrome) + path;
    };

    useEffect(() => {
        setOrgId(getOrgId(accessToken!));
    }, [])

    return (
        <Form>
            <Title headingLevel="h2">Configure URLs</Title>
            <Text className="pf-v5-u-color-400 pf-v5-u-font-size-sm">Some Identity Provider Services require additional
                configuration when enabling third party single sign on. Please review the following information and
                verify that these are filled in where required in your company IDP.</Text>

            <FormGroup label="Metadata URL">
                <ClipboardCopy isReadOnly clickTip="copied">
                    {getConfigURL(`broker/${orgId}/endpoint/descriptor`)}
                </ClipboardCopy>
            </FormGroup>

            <FormGroup label="Redirect URL or ACS (Assertion Consumption Service) URL">
                <ClipboardCopy isReadOnly clickTip="copied">
                    {getConfigURL(`broker/${orgId}/endpoint`)}
                </ClipboardCopy>
            </FormGroup>
        </Form>
    )
}

export default ImportSamlFromURL;
