/*
 * SAML 2.0 specs
 * https://docs.oasis-open.org/security/saml/v2.0/saml-schema-metadata-2.0.xsd
 * https://docs.oasis-open.org/security/saml/v2.0/saml-metadata-2.0-os.pdf
 */

export class SamlMetadataConstants {

    /*
     * @_ is a fast-xml-parser prefix convention to differentiate between XML attributes and element content.
     * The @ symbol signifies that it's an attribute, and the _ is added to the prefix to ensure clarity.
     */

    public static readonly ENTITY_ID = '@_entityID';

    public static readonly BINDING = '@_Binding';

    public static readonly LOCATION = '@_Location';

    public static readonly KEY_INFO = 'KeyInfo';

    public static readonly X509_DATA = 'X509Data';

    public static readonly X509_CERTIFICATE = 'X509Certificate';

    public static readonly DS_KEY_INFO = 'ds:KeyInfo';

    public static readonly DS_X509_DATA = 'ds:X509Data';

    public static readonly DS_X509_CERTIFICATE = 'ds:X509Certificate';

    public static readonly HTTP_POST_BINDING = 'urn:oasis:names:tc:SAML:2.0:bindings:HTTP-POST';

    public static readonly HTTP_REDIRECT_BINDING = 'urn:oasis:names:tc:SAML:2.0:bindings:HTTP-Redirect';
}
