import React, {MutableRefObject, useState} from 'react';
import {
    TextInput,
    Form,
    FormGroup,
    HelperText,
    HelperTextItem, TextArea
} from '@patternfly/react-core';
import {processAllInputs, setStateValue} from '../../../Utils/general-utils';
import {InputValidation} from "@apps/IdpSelfService/CommonInterfaces/input-validation";
import {UserChoices} from "@apps/IdpSelfService/CommonInterfaces/user-choices";
import {AUTH_REQUEST_URL, ENTITY_ID, X509_CERTIFICATE, SERVICE_PROVIDER_ENTITY_ID} from "../../../Apps/IdpSelfService/CommonInterfaces/idp-request-types";

interface Props {
    updateStatus: Function;
    resetNextClicked: Function;
    rootValidation: MutableRefObject<InputValidation>;
    rootUserChoices: MutableRefObject<UserChoices>;
}
const ImportSamlManual = ({updateStatus, resetNextClicked, rootValidation, rootUserChoices}: Props) => {
    const [selections, setSelections] = useState();

    // eslint-disable-next-line no-unused-vars
    const [validations, setValidations] = useState({
        identityProviderEntityId: {
            inputType: 'DOMAIN',
            status: 'default',
            message: ''
        },
        authenticationRequestsUrl: {
            inputType: 'DOMAIN',
            status: 'default',
            message: ''
        },
        x509Certificate: {
            inputType: 'GENERAL',
            status: 'default',
            message: ''
        }
    });

    /**
     * updates the information being used to govern the application flow as well as the final api update object.
     * @param value
     */
    const handleSelection = (value: any) => {

        /*
         * regardless of current state. if the user alters the input clear existing flags. This should also reset
         * the currently displayed errors
         */
        resetNextClicked(false);

        //  update the main data storage object with the new value
        rootUserChoices.current.selections[value.target.id] = value.target.value;

        /*
         * update the validation settings. It is expected that the id's used in the validation object matches
         * the id's used on the elements.
         */
        const failureTriggered = processAllInputs(rootUserChoices.current.selections, validations, setValidations);

        rootValidation.current.IMPORT_SAML_MANUALLY = !failureTriggered;

        if (!failureTriggered) {
            updateStatus("FINAL_STEP");
        }
    }

    return <Form>
        <FormGroup
            label="Identity Provider Entity ID"
            isRequired
            onChange={(value) => { handleSelection(value) }}>
            <HelperText>
                <HelperTextItem>The entityID used to validate the Issuer for received SAML assertions.</HelperTextItem>
            </HelperText>
            <TextInput
                onChange={(targetElement) => { setStateValue(targetElement.currentTarget.value, ENTITY_ID, selections, setSelections) }}
                type="text"
                id={ENTITY_ID} />
        </FormGroup>

        <FormGroup
            label="Authentication Request URL"
            isRequired
            onChange={(value) => { handleSelection(value) }}>
            <HelperText>
                <HelperTextItem>The URL that sends SAML authentication requests</HelperTextItem>
            </HelperText>
            <TextInput onChange={(formElement) => { setStateValue(formElement.currentTarget.value, AUTH_REQUEST_URL, selections, setSelections) }}
                       type="text"
                       id={AUTH_REQUEST_URL} />
        </FormGroup>

        <FormGroup
            label="Service Provider Entitiy ID"
            isRequired
            onChange={(value) => { handleSelection(value) }}>
            <HelperText>
                <HelperTextItem>The URL that sends SAML authentication requests</HelperTextItem>
            </HelperText>
            <TextInput onChange={(formElement) => { setStateValue(formElement.currentTarget.value, AUTH_REQUEST_URL, selections, setSelections) }}
                       type="text"
                       id={SERVICE_PROVIDER_ENTITY_ID} />
        </FormGroup>

        <FormGroup
            label="x509 Certificate"
            isRequired
            onChange={(value) => { handleSelection(value) }}>
            <HelperText>
                <HelperTextItem>A PEM certificate that is used to check for signatures.</HelperTextItem>
            </HelperText>
            <TextArea onChange={(formElement) => { setStateValue(formElement.currentTarget.value, X509_CERTIFICATE, selections, setSelections) }}
                      resizeOrientation="vertical"
                      type="text"
                      id={X509_CERTIFICATE}/>
        </FormGroup>

    </Form>

}

export default ImportSamlManual;
