import React from 'react';
import { WizardFooterWrapper} from '@patternfly/react-core/dist/esm/components/Wizard';
import { Button } from '@patternfly/react-core';

const FinalizeFooter = () => {

    /**
     * Governs what happens when the next button gets clicked. Important for allowing the back and forth communication
     * between the footer element and the actual component being displayed when needing to perform steps such as validation.
     */
    const handleNextClick = () => {
        window.location.reload();
    }

    return (
        <WizardFooterWrapper>
            <Button onClick={handleNextClick}>Submit</Button>
        </WizardFooterWrapper>
    );
}

export default FinalizeFooter;
