import {
    Form,
    FormGroup, Title
} from '@patternfly/react-core';
import React, {ReactElement, useEffect, useState} from 'react';
import PropTypes from 'prop-types';
import {InputValidation} from "../CommonInterfaces/input-validation";
import {UserChoices} from "../CommonInterfaces/user-choices";
import {SamlMetadataFile} from "../../../Components/FileUpload/SamlMetadataFile/saml-metadata-file";

interface Props {
    updateStatus: Function;
    resetNextClicked: Function;
    rootValidation: React.MutableRefObject<InputValidation>;
    rootUserChoices: React.MutableRefObject<UserChoices>;
}

const ImportSamlFromFile = ({updateStatus, resetNextClicked, rootValidation, rootUserChoices}: Props) => {
    const [samlMetadataFile, setSamlMetadataFile] = useState<ReactElement>()

    useEffect(() => {
        setSamlMetadataFile(<SamlMetadataFile updateStatus={updateStatus}
                                              resetNextClicked={resetNextClicked}
                                              rootValidation={rootValidation}
                                              rootUserChoices={rootUserChoices}
                                              maxKBSize={ 100 }></SamlMetadataFile>);
    }, []);

    return <Form>
        <Title headingLevel="h2">Import from file</Title>
        <FormGroup label="File Upload" isRequired>
            {samlMetadataFile}
        </FormGroup>
    </Form>
}

ImportSamlFromFile.propTypes = {
    rootUserChoices: PropTypes.object,
    rootValidation: PropTypes.object,
    updateStatus: PropTypes.func
}

export default ImportSamlFromFile;
