/**
 * check for potentially dangerous XML elements:
 * - XXE (XML External Entity)
 * - XML Bomb (Billion Laughs Attack)
 * - Recursive Entity defs
 */
export function containsPotentialThreats(xmlString: string) {
    return xmlString.includes('<!DOCTYPE') ||
        xmlString.includes('<!ENTITY') ||
        xmlString.includes('SYSTEM') ||
        ((xmlString.match(/<!ENTITY/gu) || []).length > 5) ||
        (xmlString.includes('&') && !(/&[a-zA-Z0-9]+;/u).test(xmlString));
}
