/**
 * Formats a string message by replacing placeholders with provided arguments.
 * Currently, it supports both {n} style placeholders and %s for strings and %d for numbers.
 *
 * @param message - string message template containing placeholders.
 * @param args - any umber of string or number arguments to replace placeholders.
 * @returns formatted message.
 */
export function formatMessage(message: string, ...args: (string | number)[]): string {
    if (!message) {
        return '';
    }

    // eslint-disable-next-line no-unused-vars
    let formattedMessage = message.replace(/%s|%d/g, (match) => {
        const arg = args.shift();
        return String(arg);
    });

    // eslint-disable-next-line require-unicode-regexp
    formattedMessage = formattedMessage.replace(/{(?:\d+)}/g, (match, index) => {
        const arg = args[parseInt(index, 10)];
        // eslint-disable-next-line no-undefined
        return arg !== undefined ? String(arg) : match;
    });

    return formattedMessage;
}
